import { ThemeProvider as EmotionThemeProvider } from '@emotion/react'
import React, { createContext } from 'react'
import { useState } from 'react'

import darkTheme from '../themes/dark'
import lightTheme from '../themes/light'

export const themes = {
  ['light']: lightTheme,
  ['dark']: darkTheme,
}

const INITIAL_THEME = 'dark';

type ThemeNameType = keyof typeof themes;

type Props = {
  children: React.ReactChild
}

type ThemeNameContextType = {
  setThemeName: (v: ThemeNameType) => void,
  themeName: ThemeNameType
}

const initialThemeName: ThemeNameContextType = { setThemeName: () => undefined, themeName: INITIAL_THEME };

export const ThemeNameContext = createContext<ThemeNameContextType>(initialThemeName);

export const ThemeProvider = ({ children }: Props) => {
  const [themeName, setThemeName] = useState<ThemeNameType>(() => initialThemeName.themeName)

  return <ThemeNameContext.Provider value={{setThemeName, themeName}}>
    <EmotionThemeProvider theme={themes[themeName]}>
      {children}
    </EmotionThemeProvider>
  </ThemeNameContext.Provider>
}
